.app {
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.app-header {
  background-color: #FF6F69;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}
.form-input{
  height: 4vh;
  width: 20vw;
  border-radius: 5px;
  border: none;
  padding: 5px;
  margin-bottom: 5px;
  filter: drop-shadow(0 0 0.25rem #cbd1d4);
}
.form-button{
  border-radius: 5px;
  font-weight: bold;
  border: none;
  margin-left: 5px;
  padding: 8px;
  font-size: medium;
}
